<template>
  <view class="tasks-container page-container">
    <view class="filter-tabs">
      <view
        class="tab"
        :class="{ active: activeTab === 'all' }"
        @click="changeTab('all')"
      >
        <text class="tab-text">ÂÖ®ÈÉ®‰ªªÂä°</text>
      </view>
      <view
        class="tab"
        :class="{ active: activeTab === 'pending' }"
        @click="changeTab('pending')"
      >
        <text class="tab-text">Êú™ÂºÄÂßã</text>
      </view>
      <view
        class="tab"
        :class="{ active: activeTab === 'in_progress' }"
        @click="changeTab('in_progress')"
      >
        <text class="tab-text">ËøõË°å‰∏≠</text>
      </view>
      <view
        class="tab"
        :class="{ active: activeTab === 'completed' }"
        @click="changeTab('completed')"
      >
        <text class="tab-text">Â∑≤ÂÆåÊàê</text>
      </view>
      <view
        class="tab"
        :class="{ active: activeTab === 'overdue' }"
        @click="changeTab('overdue')"
      >
        <text class="tab-text">Â∑≤ËøáÊúü</text>
      </view>
    </view>

    <view class="sort-container">
      <picker
        class="sort-picker"
        :range="sortOptions"
        :value="currentSortIndex"
        @change="handleSortChange"
      >
        <view class="sort-text">
          {{ sortOptions[currentSortIndex] }}
          <text class="sort-icon">‚ñº</text>
        </view>
      </picker>
    </view>

    <scroll-view
      scroll-y="true"
      :refresher-triggered="triggered"
      class="tasks-list"
      refresher-enabled="true"
      @refresherpulling="onPulling"
      @refresherrefresh="onRefresh"
    >
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view v-if="loading" class="loading-container">
        <view class="loading-spinner"></view>
        <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
      </view>

      <!-- ‰ªªÂä°ÂàóË°® -->
      <view v-else-if="tasks.length > 0">
        <view
          class="task-card"
          v-for="(task, index) in tasks"
          :key="task.id"
          :class="getTaskPriorityClass(task.priority)"
          @click="toTaskDetail(task)"
        >
          <view class="task-header">
            <view class="priority-badge" :class="task.priority">
              <text class="priority-text">{{
                getPriorityText(task.priority)
              }}</text>
            </view>
            <text class="deadline-time">{{ formatTime(task.deadline) }}</text>
          </view>

          <view class="task-content">
            <text class="task-title">{{ task.taskName || "Êó†Ê†áÈ¢ò‰ªªÂä°" }}</text>
            <text class="task-description">{{
              task.description || "Êó†‰ªªÂä°ÊèèËø∞"
            }}</text>
            <text class="task-course">{{
              task.subjectName || "ÈÄöÁî®ËØæÁ®ã"
            }}</text>
          </view>

          <view class="task-footer">
            <view class="priority-info">
              <text class="priority-dot" :class="task.priority"></text>
              <text class="priority-label">{{
                getPriorityLabel(task.priority)
              }}</text>
            </view>

            <view class="task-actions">
              <view v-if="task.hasAttachment" class="attachment-icon">
                <text>üìé</text>
              </view>

              <view class="status-buttons">
                <view
                  class="status-btn"
                  :class="{ active: task.status === 'Êú™ÂÆåÊàê' }"
                  @click="updateTaskStatus(task, 'pending')"
                >
                  Êú™ÂÆåÊàê
                </view>
                <view
                  class="status-btn"
                  :class="{ active: task.status === 'Â∑≤ÂÆåÊàê' }"
                  @click="updateTaskStatus(task, 'completed')"
                >
                  Â∑≤ÂÆåÊàê
                </view>
              </view>
            </view>
          </view>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view v-else class="empty-container">
        <view class="empty-icon">üìù</view>
        <text class="empty-text">ÊöÇÊó†‰ªªÂä°</text>
        <text class="empty-subtext">ÁÇπÂáªÂè≥‰∏ãËßíÊåâÈíÆÊ∑ªÂä†Êñ∞‰ªªÂä°</text>
      </view>
    </scroll-view>

    <view class="add-task-btn" @click="navigateToAddTask">
      <text class="add-task-icon">+</text>
    </view>
  </view>
</template>

<script>
import { getTaskList, updateTaskStatus } from "../../api/task";

export default {
  data() {
    return {
      activeTab: "all",
      triggered: false,
      currentSortIndex: 0,
      sortOptions: ["Êåâ‰ºòÂÖàÁ∫ßÊéíÂ∫è", "ÊåâÊà™Ê≠¢Êó∂Èó¥ÊéíÂ∫è", "ÊåâÂàõÂª∫Êó∂Èó¥ÊéíÂ∫è"],
      tasks: [], // ÂàùÂßãÂåñ‰∏∫Á©∫Êï∞ÁªÑÔºå‰ªéÊúçÂä°Âô®Ëé∑ÂèñÊï∞ÊçÆ
      loading: false, // Ê∑ªÂä†Âä†ËΩΩÁä∂ÊÄÅ
      total: 0, // ÊÄª‰ªªÂä°Êï∞
      currentPage: 1, // ÂΩìÂâçÈ°µÁ†Å
      pageSize: 20, // ÊØèÈ°µÂ§ßÂ∞è
    };
  },

  onLoad() {
    // È°µÈù¢Âä†ËΩΩÊó∂Ëé∑Âèñ‰ªªÂä°ÂàóË°®
    this.fetchTasksList();
  },

  methods: {
    // Ëé∑Âèñ‰ªªÂä°ÂàóË°®ÁöÑÊñπÊ≥ï
    async fetchTasksList() {
      try {
        // ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        this.loading = true;

        const query = {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          status: this.activeTab === "all" ? "" : this.activeTab,
        };

        const res = await getTaskList(query);

        // Ê£ÄÊü•ÂìçÂ∫îÊï∞ÊçÆÁªìÊûÑ
        if (res && res.data) {
          this.tasks = res.data.records || [];
          this.total = res.data.total || 0;
        } else {
          this.tasks = [];
          this.total = 0;
          console.warn("ÂìçÂ∫îÊï∞ÊçÆÁªìÊûÑ‰∏çÁ¨¶ÂêàÈ¢ÑÊúü:", res);
        }
      } catch (error) {
        console.error("Ëé∑Âèñ‰ªªÂä°ÂàóË°®Â§±Ë¥•:", error);
        this.tasks = [];
        this.total = 0;

        // Êèê‰æõÊõ¥ÂèãÂ•ΩÁöÑÈîôËØØÊèêÁ§∫
        uni.showToast({
          title: "Ëé∑Âèñ‰ªªÂä°ÂàóË°®Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï",
          icon: "none",
          duration: 2000,
        });
      } finally {
        // Êó†ËÆ∫ÊàêÂäüÂ§±Ë¥•ÈÉΩÂÖ≥Èó≠Âä†ËΩΩÁä∂ÊÄÅ
        this.loading = false;
      }
    },
    handleSortChange(e) {
      this.currentSortIndex = e.detail.value;
      
      // ÊéíÂ∫èÂêé‰∏çÈúÄË¶ÅÈáçÊñ∞ËØ∑Ê±ÇÔºåÁõ¥Êé•Âú®ÂâçÁ´ØÊéíÂ∫è
      this.sortTasks();
    },
    // ÂâçÁ´ØÊéíÂ∫èÊñπÊ≥ï
    sortTasks() {
      const sortedTasks = [...this.tasks];

      switch (this.currentSortIndex) {
        case 0: // Êåâ‰ºòÂÖàÁ∫ßÊéíÂ∫è
          // ‰ºòÂÖàÁ∫ßÊéíÂ∫èÔºöÈ´ò > ‰∏≠ > ‰Ωé
          sortedTasks.sort((a, b) => {
            // ‰ΩøÁî®‰∏≠ÊñáÈîÆÂêçÂåπÈÖçÊï∞ÊçÆ
            const priorityOrder = { È´ò: 0, ‰∏≠: 1, ‰Ωé: 2 };
            return (
              (priorityOrder[a.priority] || 3) -
              (priorityOrder[b.priority] || 3)
            );
          });
          break;
        case 1: // ÊåâÊà™Ê≠¢Êó∂Èó¥ÊéíÂ∫è
          // Êà™Ê≠¢Êó∂Èó¥ÊéíÂ∫è
          sortedTasks.sort((a, b) => {
            return new Date(a.deadline || 0) - new Date(b.deadline || 0);
          });
          break;
        case 2: // ÊåâÂàõÂª∫Êó∂Èó¥ÊéíÂ∫è
          // ÂàõÂª∫Êó∂Èó¥ÊéíÂ∫èÔºàÂÅáËÆæÂ≠óÊÆµÂêçÔºâ
          sortedTasks.sort((a, b) => {
            return new Date(b.createTime || 0) - new Date(a.createTime || 0); // ÊúÄÊñ∞ÁöÑÂú®Ââç
          });
          break;
      }

      this.tasks = sortedTasks;
    },
    // ÂàáÊç¢Ê†áÁ≠æÈ°µÁöÑÊñπÊ≥ï
    changeTab(tab) {
      this.activeTab = tab;
      this.fetchTasksList(); // ÂàáÊç¢Ê†áÁ≠æÂêéÈáçÊñ∞Ëé∑Âèñ‰ªªÂä°ÂàóË°®
    },
    // Êõ¥Êñ∞‰ªªÂä°Áä∂ÊÄÅÁöÑÊñπÊ≥ï
    async updateTaskStatus(task, status) {
      try {
        // ÈòªÊ≠¢‰∫ã‰ª∂ÂÜíÊ≥°ÔºåÈÅøÂÖçËß¶ÂèëÂç°ÁâáÁÇπÂáª‰∫ã‰ª∂
        if (event) event.stopPropagation();

        // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
        uni.showLoading({
          title: "Êõ¥Êñ∞‰∏≠...",
        });

        // Ë∞ÉÁî®APIÊõ¥Êñ∞‰ªªÂä°Áä∂ÊÄÅ
        await updateTaskStatus(task.id, status);

        // Êõ¥Êñ∞Êú¨Âú∞Êï∞ÊçÆ
        const taskIndex = this.tasks.findIndex((t) => t.id === task.id);
        if (taskIndex !== -1) {
          this.$set(this.tasks[taskIndex], "status", status);
          // Â¶ÇÊûúÊòØÂàáÊç¢Âà∞Â∑≤ÂÆåÊàêÁä∂ÊÄÅÔºåËÆæÁΩÆÂÆåÊàêÊó∂Èó¥
          if (status === "completed") {
            this.$set(
              this.tasks[taskIndex],
              "completedTime",
              new Date().toISOString()
            );
          }
        }

        uni.showToast({
          title: "‰ªªÂä°Áä∂ÊÄÅÂ∑≤Êõ¥Êñ∞",
          icon: "success",
        });
      } catch (error) {
        console.error("Êõ¥Êñ∞‰ªªÂä°Áä∂ÊÄÅÂ§±Ë¥•:", error);
        uni.showToast({
          title: "Êõ¥Êñ∞‰ªªÂä°Áä∂ÊÄÅÂ§±Ë¥•",
          icon: "none",
        });
      } finally {
        // ÈöêËóèÂä†ËΩΩÊèêÁ§∫
        uni.hideLoading();
      }
    },
    getTaskPriorityClass(priority) {
      return {
        "high-priority": priority === "È´ò",
        "medium-priority": priority === "‰∏≠",
        "low-priority": priority === "‰Ωé",
      };
    },
    getPriorityText(priority) {
      const texts = {
        high: "È´ò‰ºòÂÖàÁ∫ß",
        medium: "ÊôÆÈÄö‰ºòÂÖàÁ∫ß",
        low: "ÊôÆÈÄö‰ºòÂÖàÁ∫ß",
      };
      return texts[priority] || "";
    },
    getPriorityLabel(priority) {
      const labels = {
        high: "È´ò‰ºòÂÖàÁ∫ß",
        medium: "‰∏≠Á≠â‰ºòÂÖàÁ∫ß",
        low: "‰Ωé‰ºòÂÖàÁ∫ß",
      };
      return labels[priority] || "";
    },
    navigateToAddTask() {
      uni.navigateTo({
        url: "/pages/tasks/add-task",
      });
    },
    toTaskDetail(task) {
      uni.navigateTo({
        url: "/pages/tasks/task-detail?id=" + task.taskId,
      });
    },
    // Ê†ºÂºèÂåñÊó∂Èó¥ÁöÑÊñπÊ≥ï
    formatTime(time) {
      if (!time) return "ÊöÇÊó†Êà™Ê≠¢Êó∂Èó¥";
      try {
        const date = new Date(time);
        const now = new Date();
        const diffTime = Math.abs(date - now);
        const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));

        if (diffDays === 0) {
          return (
            "‰ªäÂ§© " +
            date.getHours().toString().padStart(2, "0") +
            ":" +
            date.getMinutes().toString().padStart(2, "0")
          );
        } else if (diffDays === 1) {
          return (
            "ÊòéÂ§© " +
            date.getHours().toString().padStart(2, "0") +
            ":" +
            date.getMinutes().toString().padStart(2, "0")
          );
        } else if (diffDays < 7) {
          return diffDays + "Â§©Âêé";
        } else {
          return (
            date.getFullYear() +
            "-" +
            (date.getMonth() + 1).toString().padStart(2, "0") +
            "-" +
            date.getDate().toString().padStart(2, "0")
          );
        }
      } catch (error) {
        return time;
      }
    },

    // ‰∏ãÊãâÂà∑Êñ∞Áõ∏ÂÖ≥ÊñπÊ≥ï
    onPulling() {
      var that = this;
      if (!this.triggered) {
        //‰∏ãÊãâÂä†ËΩΩÔºåÂÖàËÆ©ÂÖ∂ÂèòtrueÂÜçÂèòfalseÊâçËÉΩÂÖ≥Èó≠
        this.triggered = true;
        //ÂÖ≥Èó≠Âä†ËΩΩÁä∂ÊÄÅ (ËΩ¨Âä®ÁöÑÂúà)ÔºåÈúÄË¶Å‰∏ÄÁÇπÂª∂Êó∂ÊâçËÉΩÂÖ≥Èó≠
        setTimeout(() => {
          that.triggered = false;
        }, 1000);
      }
    },

    onRefresh() {
      // ÈáçÁΩÆÈ°µÁ†Å‰∏∫Á¨¨‰∏ÄÈ°µ
      this.currentPage = 1;
      // ÈáçÊñ∞Ëé∑ÂèñÊï∞ÊçÆ
      this.fetchTasksList().then(() => {
        // Êï∞ÊçÆÂä†ËΩΩÂÆåÊàêÂêéÔºåÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞Âä®Áîª
        uni.stopPullDownRefresh();
      });
    },
  },
};
</script>

